import React, {useEffect, useState} from 'react';
import Navbar from '../../ui/Navbar/Navbar';
import './Articles.css';

interface Article {
    id: number;
    author: string;
    title: string;
    content: string;
    summary: string;
}

const articles: Article[] = [
    {
        id: 1,
        author: 'John Doe',
        title: 'Capturing Time: The Artistry of Photography',
        content: 'In the realm of art, photography stands as a timeless medium, freezing moments that transcend the boundaries of existence. With the click of a shutter, a photographer transforms fleeting instances into everlasting visual poetry. Each photograph becomes a brushstroke on the canvas of memory, capturing the nuances of emotion, light, and shadow.\n' +
            '\n' +
            'Photography is more than just a mechanical process; it is an intimate dance between the artist and the world. Through the lens, photographers weave narratives, telling stories that resonate across cultures and time. A single photograph can evoke a myriad of emotions, transporting viewers to the heart of the artist\'s vision.\n' +
            '\n' +
            'In this dance of light and perception, photographers emerge as storytellers, poets, and architects of visual symphonies. They navigate the delicate balance between technical precision and creative intuition, creating compositions that challenge, provoke, and inspire. In the hands of a skilled photographer, a simple frame becomes a portal to explore the depth of human experience.\n' +
            '\n' +
            'In the artistry of photography, time becomes malleable, allowing us to revisit the past, witness the present, and dream of the future. Through the lens, we glimpse the world as seen through the eyes of the artist—a world where every detail is intentional, every shadow pregnant with meaning.',
        summary: 'Learn the basics of React Hooks and how to use them in your projects.',
    },
    {
        id: 2,
        author: 'Jane Smith',
        title: 'The Palette of Light: Photography as Visual Art',
        content: 'This is the full content of the TypeScript article.',
        summary: 'A beginner-friendly guide to getting started with TypeScript in your projects.',
    },
    // Add more articles as needed
];

function ArticleList(){

    const [selectedArticle, setSelectedArticle] = useState<Article | null>(null);

    const handleArticleClick = (article: Article) => {
        setSelectedArticle(article);
    };

    const handleGoBack = () => {
        setSelectedArticle(null);
    };

    useEffect(() => {
        console.log("Articles component rendered");
        // ... rest of your code
    }, []);

    return (
        <div>

            {selectedArticle ? (
                // Full Article View
                <div className="full-article">
                    <h2>{selectedArticle.title}</h2>
                    <p>Author: {selectedArticle.author}</p>
                    <p>{selectedArticle.content}</p>
                    <button onClick={handleGoBack}>Go Back</button>
                </div>
            ) : (
                // Article Previews List View
                <div className="acontainer">
                    <h1>Editorials</h1>
                    <ul>
                        {articles.map((article) => (
                            <li key={article.id} onClick={() => handleArticleClick(article)}>
                                <h3>{article.title}</h3>
                                <p>Author: {article.author}</p>
                                <p>{article.summary}</p>
                            </li>
                        ))}
                    </ul>
                </div>
            )}
        </div>
    );
};

export default ArticleList;
